import React from 'react';

import connect from '@vkontakte/vk-connect';
import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';

import {Panel, Alert, Group, Button, PanelHeader, Cell, Avatar} from "@vkontakte/vkui"
import * as VK from '../../services/VK';

class HomePanelBase extends React.Component {


    state = {
        groups: {
            admined: [],
            other: [],
        },
        loading: true,
        errorGetAuthToken: false,
        showImg: false
    };

    showImg = () => {
        this.setState({showImg: true});
    };

    openPopout() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: 'Нет',
                    autoclose: true,
                    style: 'cancel',
                }, {
                    title: 'Да',
                    autoclose: true,
                    action: this.showImg
                }]}
                onClose={() => this.props.closePopout()}
            >
                <h2>Вопрос значит</h2>
                <p>Вас роняли в детстве?</p>
            </Alert>
        );
    }



    async componentDidMount() {
      const profile = connect.send("VKWebAppGetUserInfo");
      console.log(profile);
    }
    render() {
        const {id} = this.props;

        return (
            <Panel id={id}>
                <PanelHeader>Профиль</PanelHeader>
                <Group>
                  <Cell
                    photo="https://pp.userapi.com/c841034/v841034569/3b8c1/pt3sOw_qhfg.jpg"
                    description="VKontakte"

                    before={<Avatar src="https://pp.userapi.com/c841034/v841034569/3b8c1/pt3sOw_qhfg.jpg" size={80}/>}
                    size="l"
                  >
                    Артур Стамбульцян
                  </Cell>
                </Group>
            </Panel>
        );
    }

}

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default HomePanelBase;
