import React from 'react';
import {connect} from 'react-redux';

import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';

import {Panel, Alert, Group, PanelHeader, Gallery} from "@vkontakte/vkui"

class Market extends React.Component {

    state = {
        showImg: false
    };

    showImg = () => {
        this.setState({showImg: true});
    };

    openPopout() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: 'Нет',
                    autoclose: true,
                    style: 'cancel',
                }, {
                    title: 'Да',
                    autoclose: true,
                    action: this.showImg
                }]}
                onClose={() => this.props.closePopout()}
            >
                <h2>Вопрос значит</h2>
                <p>Вас роняли в детстве?</p>
            </Alert>
        );
    }

    render () {
        const {id} = this.props;
          return (
              <Panel id={id}>
                <PanelHeader>Магазин</PanelHeader>
                <Group title="Centered">
                  <Gallery
                    slideWidth="90%"
                    align="center"
                    style={{ height: 150 }}
                  >
                    <div style={{ backgroundColor: 'var(--destructive)' }} />
                    <div style={{ backgroundColor: 'var(--button_commerce_background)' }} />
                    <div style={{ backgroundColor: 'var(--accent)' }} />
                  </Gallery>
                </Group>
                <Group title="Centered">
                  <Gallery
                    slideWidth="90%"
                    align="center"
                    style={{ height: 150 }}
                  >
                    <div style={{ backgroundColor: 'var(--destructive)' }} />
                    <div style={{ backgroundColor: 'var(--button_commerce_background)' }} />
                    <div style={{ backgroundColor: 'var(--accent)' }} />
                  </Gallery>
                </Group>
                <Group title="Centered">
                  <Gallery
                    slideWidth="90%"
                    align="center"
                    style={{ height: 150 }}
                  >
                    <div style={{ backgroundColor: 'var(--destructive)' }} />
                    <div style={{ backgroundColor: 'var(--button_commerce_background)' }} />
                    <div style={{ backgroundColor: 'var(--accent)' }} />
                  </Gallery>
                </Group>
                <Group title="Centered">
                  <Gallery
                    slideWidth="90%"
                    align="center"
                    style={{ height: 150 }}
                  >
                    <div style={{ backgroundColor: 'var(--destructive)' }} />
                    <div style={{ backgroundColor: 'var(--button_commerce_background)' }} />
                    <div style={{ backgroundColor: 'var(--accent)' }} />
                  </Gallery>
                </Group>
              </Panel>
          )
        }
      }


const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default connect(null, mapDispatchToProps)(Market);
